import axios from '@/utils'
export default {
  getToken: (data, cb) => { // 认证token
    return axios(`/api/v1/token`, data, 'get', cb)
  },
  // 加密接口****
  aesGetCode: (data, cb) => { // 发送短信验证码
    return axios(`/api/get/code/aes`, data, `post`, cb)
  },
  // aescheckCode: (data, cb) => { // 验证短信验证码
  //   return axios(`/api/check/code/aes`, data, `post`, cb)
  // },
  aesregister: (data, cb) => { // 注册
    return axios(`/api/v1/register/aes`, data, `post`, cb)
  },
  aesverifyLogin: (data, cb) => { // 快捷登录
    return axios(`/api/v1/verify/login/aes`, data, `post`, cb)
  },
  aespwdLogin: (data, cb) => { // 密码登录
    return axios(`/api/v1/login/aes`, data, `post`, cb)
  },
  aesretrievePwd: (data, cb) => { // 找回密码
    return axios(`/api/v1/retrieve/pwd/aes`, data, `post`, cb)
  },
  // ****
  getCode: (data, cb) => { // 发送短信验证码
    return axios(`/api/get/code`, data, `post`, cb)
  },
  // checkCode: (data, cb) => { // 验证短信验证码
  //   return axios(`/api/check/code`, data, `post`, cb)
  // },
  register: (data, cb) => { // 注册
    return axios(`/api/v1/register`, data, `post`, cb)
  },
  verifyLogin: (data, cb) => { // 快捷登录
    return axios(`/api/v1/verify/login`, data, `post`, cb)
  },
  pwdLogin: (data, cb) => { // 密码登录
    return axios(`/api/v1/login`, data, `post`, cb)
  },
  retrievePwd: (data, cb) => { // 找回密码
    return axios(`/api/v1/retrieve/pwd`, data, `post`, cb)
  },
  logout: (data, cb) => { // 退出登录
    return axios(`/api/v1/logout`, data, 'get', cb)
  },
  profile: (data, cb) => { // 个人资料
    return axios(`/api/v1/profile`, data, 'get', cb)
  },
  help: (data, cb) => { // 新手帮助
    return axios(`/api/v1/help`, data, 'get', cb)
  },
  about: (data, cb) => { // 关于我们
    return axios(`/api/v1/about`, data, 'get', cb)
  },
  agreement: (data, cb) => { // 用户注册协议
    return axios(`/api/v1/agreement`, data, 'get', cb)
  },
  circle: (data, cb) => { // 引客来圈子内容
    return axios(`/api/v1/circle`, data, 'get', cb)
  },
  feedbackcate: (data, cb) => { // 用户反馈 类型
    return axios(`/api/v1/feedback/cate`, data, 'get', cb)
  },
  feedback: (data, cb) => { // 用户反馈
    return axios(`/api/v1/feedback`, data, `post`, cb)
  },
  productApply: (page, data, cb) => { // 贷款申请意向
    return axios(`/api/v1/product/apply?page=${page}`, data, 'get', cb)
  },
  creditApply: (data, cb) => { // 信用卡申请意向
    return axios(`/api/v1/apply/credit`, data, 'get', cb)
  },
  apply: (data, cb) => { // 用户申请信用卡/产品
    return axios(`/api/v1/apply`, data, `post`, cb)
  },
  comment: (data, cb) => { // 对信用卡/贷款进行评论
    return axios(`/api/v1/comment`, data, `post`, cb)
  },
  collectionProduct: (page, data, cb) => { // 贷款的收藏列表
    return axios(`/api/v1/collection/product?page=${page}`, data, 'get', cb)
  },
  collectionCredit: (page, data, cb) => { // 信用卡收藏列表
    return axios(`/api/v1/collection/credit?page=${page}`, data, 'get', cb)
  },
  collectionArticle: (page, data, cb) => { // 资讯收藏列表
    return axios(`/api/v1/collection/article?page=${page}`, data, 'get', cb)
  },
  article: (page, data, cb) => { // 资讯列表
    return axios(`/api/v1/article?page=${page}`, data, 'get', cb)
  },
  articleDetail: (id, city, data, cb) => { // 资讯详情
    return axios(`/api/v1/article/${id}?city=${city}`, data, 'get', cb)
  },
  credit: (page, data, cb) => { // 信用卡列表
    return axios(`/api/v1/credit?page=${page}`, data, 'get', cb)
  },
  creditDetail: (id, from, data, cb) => { // 信用卡详情
    return axios(`/api/v1/credit/${id}?from=${from}`, data, 'get', cb)
  },
  comments: (type, id, page, good, data, cb) => { // 评论管理
    good = good ? `/${good}` : ``
    return axios(`/api/v1/comment/${type}/${id}${good}?page=${page}`, data, 'get', cb)
  },
  loan: (page, data, cb) => { // 贷款列表
    page = page ? `${page}` : 1
    return axios(`/api/v1/loan?page=${page}`, data, `post`, cb)
  },
  loanScreen: (data, cb) => { // 贷款高级筛选
    return axios(`/api/v1/loan/screen`, data,`get`, cb)
  },
  loanDetail: (id, from, city, data, cb) => { // 贷款详情
    return axios(`/api/v1/loan/${id}?from=${from}&city=${city}`, data, 'get', cb)
  },
  praise: (id, data, cb) => { // 资讯点赞
    return axios(`/api/v1/praise/${id}`, data,'get', cb)
  },
  collect: (data, cb)=> { // 贷款/信用卡/资讯收藏操作
    return axios(`/api/v1/collect`, data, 'get', cb)
  },
  share: (data, cb) => { // 贷款/信用卡/资讯分享操作
    return axios(`/api/v1/share`, data, `post`, cb)
  },
  district: (data, cb) => { // 获取排序城市数据 / 获取三级联动城市数据
    return axios(`/api/v1/district`, data, 'get', cb)
  },
  home: (data, cb) => { // 获取首页数据
    return axios(`/api/v1/home`, data, 'get', cb)
  },
  homeBase: (data, cb) => { // 获取首页广告
    axios(`/api/v1/home/base`, data, 'get', cb)
  },
  homeCount: (data, cb) => { // 首页累计借款数
    return axios(`/api/v1/home/count`, data, 'get', cb)
  },
  homeId: (id, page, city, data, cb) => { // 获取贷款专题
    return axios(`/api/v1/home/${id}?page=${page}&city=${city}`, data, 'get', cb)
  },
  hotCity: (data, cb) => { // 热门城市数据
    return axios(`/api/v1/hot/city`, data, 'get', cb)
  },
  message: (page, data, cb) => { // 系统消息
    return axios(`/api/v1/message?page=${page}`, data, 'get', cb)
  },
  messageId: (id, data, cb) => { // 系统消息：详情
    return axios(`/api/v1/message/${id}`, data, 'get', cb)
  },
  messageNew: (data, cb) => { // 系统消息：新消息
    return axios(`/api/v1/message/check/new`, data, 'get', cb)
  },
  messageSet: (data, cb) => { // 系统消息变更
    return axios(`/api/v1/message/set/`, data, `post`, cb)
  }
}



