<template>
  <div id="app">
    <Content :class="[{footerOn: $route.meta.isFooter},{headerOn: $route.meta.isHeader}]"></Content>
    <Footer v-if="$route.meta.isFooter"></Footer>
  </div>
</template>

<script>
import Content from './views/Content'
import Footer from './views/Footer'
import checkurl from "@/utils/checkurl";

document.documentElement.style.fontSize = document.documentElement.clientWidth / 7.5 + 'px'
export default {
  name: 'App',
  components: {
    Footer, Content
  },
  mounted () {
    
  },
  created(){
    switch (checkurl('packageParams')){
      case 'hry': 
        this.setWindowTitle('引客来')
        break
      default:
        this.setWindowTitle('引客来')
    }
  },
  methods:{
    setWindowTitle (packageParams) {
      sessionStorage.packageParams = sessionStorage.packageParams || packageParams;
      window.document.title = sessionStorage.packageParams
    }
  },
  beforeCreate () {
   
  }
}
</script>

<style lang='scss'>
  @import './style/base.scss';
  @import './style/vant.scss';
  #app{
    height: 100%;
  }
</style>



