import axios from 'axios'
import router from '@/router'
import checkurl from '@/utils/checkurl'
import baseUrl from '@/api/apiGlobal'
import {
  guid
} from '@/utils/check'
// 创建axios实例
const Axios = axios.create({
  baseURL: baseUrl.api_url, // api的base_url
  timeout: 15000 // 请求超时时间
})
let profileurl = '' // profile接口不自动跳转登录

sessionStorage.Channel = checkurl('Channel') || sessionStorage.Channel || 100001
if (checkurl('Channel') && checkurl('deviceid_') && checkurl('Authentication')) {
  // console.log('注册页过来的')
  localStorage.deviceid_ = checkurl('deviceid_')
  sessionStorage.token = checkurl('Authentication') || ''
  sessionStorage.page_id = checkurl('page_id') || 0
} else {
  // console.log('正常流程')
  if (!localStorage.deviceid_ || localStorage.deviceid_ === '') {
    localStorage.deviceid_ = guid()
  }
}
// 添加请求拦截器
Axios.interceptors.request.use(config => {
  config.headers['Source'] = 'wap' // 平台
  config.headers['Version'] = '2.0.0' // 渠道下App版本
  config.headers['System'] = '1.0.0' // 操作系统版本
  config.headers['Channel'] = sessionStorage.Channel // 渠道码
  config.headers['Device'] = localStorage.deviceid_
  config.headers['Authentication'] = sessionStorage.token
  config.headers['PageId'] = sessionStorage.page_id || 0
  // 在发送请求之前做些什么
  profileurl = config.url.indexOf('profile') >= 0
  return config
}, error => {
  // 对请求错误做些什么
  // console.log(error)
  return Promise.reject(error)
})

// token过期处理
Axios.interceptors.response.use(response => {
  // 对响应数据做点什么
  if (response.data.code === 4001) { // token过期
    // Axios.get('/api/v1/token').then((res) => {
    //   if (res.code === 200) {
    //     sessionStorage.token = res.data.token
    //     // router.go(0)
    //   }
    // })
  } else if (response.data.code === 4002) {
    if (!profileurl) {
      router.push({
        path: '/login'
      })
    }
  }
  return response.data
}, error => {
  return Promise.reject(error)
})
// $http 第一层拦截
function $http (url, data, method = `get`, cb) {
  // console.log(data);
  switch (method) {
    case 'get':
      Axios.get(url, data.params ? data : {params: data}).then(res => {
        doCode(res)
      })
      break
    case 'post':
      Axios.post(url, data).then(res => {
        doCode(res)
      })
      break
    default:
  }
  function doCode (res) {
    switch (res.code) {
      case 4001:
        getToken(url, data, method, cb)
        break
      default: if (cb && typeof cb === 'function') cb(res)
    }
  }
}
// 获取token
function getToken (url, data, method = `get`, cb) {
  Axios.get('/api/v1/token').then(tk => {
    if (tk.code === 200) {
      sessionStorage.token = tk.data.token
      $http(url, data, method, cb)
    } else {
      getToken(url, data, method, cb)
    }
  })
}

// 发起请求
export default $http



